import React, { useState } from 'react'

const Checkout = () => {
    const [checkout, setcheckout] = useState(null);
    const [error, setError] = useState(null);
    const [user, setUser] = useState(null);
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        const fetchCheckout = async () => {
          try {
            const response = await postService.getCheckout();
            setcheckout(response.data.checkout);
            setError(null);
          } catch (error) {
            setError("No Checkouts items found.");
          }
        };
        fetchCheckout();
      }, []);
    
      useEffect(() => {
        fetch("http://localhost:8001/api/checkAuth", {
          method: "GET",
          credentials: "include",
        })
          .then((res) => res.json())
          .then((data) => {
            if (data.isAuthenticated) {
              setUser({
                id: data.userId,
                username: data.username,
                email: data.email,
                profile: data.profile ? data.profile : null,
              });
              setIsLoading(false);
            }
          });
      }, [router]);

  return (
    <div>{checkout.map((items,id)=>{
        return <div key={id}>{items.service_id} - {items.quantity}</div>
  
    })}</div>
  )
}

export default Checkout
